@import "montserrat.css";

body {
  margin: 0;
  padding-top: 20px;
  font-family: 'Montserrat',sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.post {
  margin: 0 auto;
  max-width: 600px;
  padding: 10px;
  background-color: #ffffff;
  border-radius: 10px;
  border: 1px solid #777777;
  margin-bottom: 40px;
}

.post__info-general {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

.post__info {
  display: flex;
}

.post__details {
  display: flex;
  margin-top: 5px;
}

.avatar {
  width: 50px;
  border-radius: 50%;
  margin-right: 20px;
}

.post__name {
  font-weight: 700;
  margin-right: 5px;
}

.post__check {
  margin-right: 10px;
}

.post__check-logo {
  width: 20px;
}

.post__nick {
  margin-right: 7px;
}

.arrow {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  transition: background-color .2s linear;
  cursor: pointer;
}

.arrow:hover {
  background-color: #e9e9e9;
}

.arrow__img {
  position: relative;
  top: 30%;
  left: 50%;
  width: 10px;
  transform: translate(-50%, -50%);
}


/*content*/
.content {
  margin-bottom: 20px;
}
.content__text {
  margin-bottom: 20px;
}

.content__img {
  width: 100%;
  height: 300px;
  border-radius: 10px;
  overflow: hidden;
}

.post__img {
  width: 100%;
}


/*feedback*/

.post__feedback {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.feedback {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  width: 120px;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color .2s linear;
}

.feedback:hover {
  background-color: #e9e9e9;
}

.feedback__img {
  width: 30px;
  margin-right: 5px;
}